:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}

.btn--blackoutline {
    background-color: transparent;
    color: #242424;
    padding: 8px 20px;
    border: 1px solid #242424;
    transition: all 0.3s ease-out;
}

.btn--large--download {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--large--download:hover {
    background: #242424;
    color: #fff;
    transition: all 0.3s ease-out;
}

.resume-btn {
    margin: 32px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    object-fit: contain;
}

resume-btn .btn {
    margin: 6px;
}
